.myform {
    text-align: center;
    margin: auto;
}
.form-check-label {
   margin-left: 8px;
}
.mainBox{
    text-align: center;
}
.inline_twoElements {
    display: inline-flex;
}
.btnSetNewPayment {
    margin-left: 4%;
    background-color: lightcoral;
    color: white;
    border-radius: 12%;
    width: 40px;

}
.btnSetNewPayment:hover {
    background-color: chartreuse;
    cursor: pointer;
}

.tableStaff {
    max-width: 1100px;
    max-height: 1100px;
    margin: 40px auto;
    text-align: center;
    border-collapse: collapse;
    border-spacing: 5px;
    background: #E1E3E0;
    border-radius: 20px;
}
.tableStaff th {
    font-size: 20px;
    font-weight: 30;
    padding: 12px 10px;
    border-bottom: 2px solid #F56433;
    color: #F56433;
}

.tableStaff td {
    padding: 10px;
    color: #8D8173;
}
.select_staff{
    width: 120px;
}
.roleColumn_staff{
    background-color: #FFCF00;
    width: 100%;
}
